import React, { useEffect, useState } from "react";
import Layout from 'layout/Layout';
import BlogDisplay from 'components/BlogDisplay';
import Metadata from 'components/Metadata';
import { BlogsPageMetadata } from 'configs/PageMetadata';
import { BlogGetAll } from "services/BlogService";
import BlogSkeleton from 'components/BlogSkeleton';
import { skipCountDefault } from 'configs/SiteConfig';

export default function BlogsPage() {
  const [skipCount, setSkipCount] = useState(0);
  const [blogs, setBlogs] = useState(null);
  const [offset, setOffset] = useState(0);

  useEffect(() => {
    BlogGetAll(skipCount)
      .then((response) => {
        if (response !== undefined) {
          if (blogs === null) {
            setBlogs(response);
          }
        }
      })
      .catch(error => {
        console.log(error);
      });
  }, [])

  useEffect(() => {
    if (typeof window !== `undefined`) {
      window.onscroll = () => {
        setOffset(window.pageYOffset);
        if ((window.innerHeight + window.scrollY + 1000) >= document.body.offsetHeight) {
          var updatedCount = skipCount + skipCountDefault;
          BlogGetAll(updatedCount)
            .then((response) => {
              if (response !== undefined) {
                if (blogs !== null) {
                  var updatedBlogs = [];
                  var x = updatedBlogs.concat(Array.from(blogs));
                  var y = x.concat(Array.from(response));
                  setBlogs(y);
                  setSkipCount(updatedCount);
                }
              }
            })
            .catch(error => {
              console.log(error);
            });
        }
      }
    }
  }, [offset])

  return (
    <Layout>
      <Metadata
        Title={BlogsPageMetadata.Title}
        Description={BlogsPageMetadata.Description}
      />
      { blogs === null ?
        <BlogSkeleton /> :
        blogs.map(function (blog, index) {
          return (
            <React.Fragment key={index}>
              <BlogDisplay blog={blog} />
            </React.Fragment>
          )
        })
      }
    </Layout>
  );
}